<script>
import axios from "axios";
import VueAxios from "vue-axios";

function aaa(file) {
  var base64Code;
  return new Promise(function(resolve) {
    var reader = new FileReader();
    reader.readAsDataURL(file);
    reader.onload = function(e) {
      // 读取到的图片base64 数据编码 将此编码字符串传给后台即可
      var imgcode = e.target.result;
      //console.log(imgcode);
      base64Code = imgcode.substring(
        imgcode.lastIndexOf(",") + 1,
        imgcode.length
      );
      resolve(base64Code);
    };
  });
}

export default {
  name: "HelloWorld",
  data() {
    return {
      msg: "Welcome to Your Vue.js App"
    };
  },
  methods: {
    async uploadPhoto(e) {
      // 利用fileReader对象获取file
      // var file = e.target.files[0];
      // var fileSize = file.size;
      // var fileName = file.name;
      var file1 = document.getElementById("file1").files[0];
      var file2 = document.getElementById("file2").files[0];
      var file3 = document.getElementById("file3").files[0];
      var fileName1 = file1.name;
      var fileName2 = file2.name;
      var fileName3 = file3.name;
      var base64Code1 = await aaa(file1);
      var base64Code2 = await aaa(file2);
      var base64Code3 = await aaa(file3);

      // 2,621,440   2M
      // if (filesize > 2101440) {
      //   // 图片大于2MB
      // }

      axios
        .post(
          "file/uploadencodefile",
          "'param'={'file1':" +
            base64Code1 +
            ",'filename1':" +
            fileName1 +
            ",'file2':" +
            base64Code2 +
            ",'filename2':" +
            fileName2 +
            ",'file3':" +
            base64Code3 +
            ",'filename3':" +
            fileName3 +
            "}"
        )
        .then(function(res) {
          var resData = res.data;
          if (resData.code == "0") {
            //0表示成功，1表示失败
            alert(resData.msg);
          } else {
            alert(resData.msg);
          }
        });
    }
  }
};
</script>

